	export default {
		data() {
			return {
				SessionKey: '',
				OpenId: '',
				code: "",
				nickName: null,
				avatarUrl: null,

				isCanUse:  false ,//默认为true
			    getphone:"1",
				writePhone:false,
				phoneNumber:"",  //手机号

			};
		},
		methods: {
			//第一授权获取用户信息===》按钮触发
			wxGetUserInfo(detail) {

                
				let _this = this;
				 // _this.setOpenid()
				uni.getUserInfo({
					provider: 'weixin',
					success: function(infoRes) {
						 console.log("phon",infoRes)
						_this.nickName = infoRes.userInfo.nickName; //昵称
						_this.avatarUrl = infoRes.userInfo.avatarUrl; //头像
                 
						_this.writePhone=true;	
                       

					},
					fail(res) {}
				});
			},

            hidePhone(){
				this.writePhone=false;
			},

			// //登录

			 login() {
				var _this = this;

				uni.showLoading({
					title: '登录中...'
				});

				// 1.wx获取登录用户code
				uni.login({
					provider: 'weixin',
					success: function(loginRes) {

						
						 _this.code = loginRes.code;
                         _this.getOpenid()

					    uni.hideLoading();

                    },
				});
			},
// 			setOpenid(){
// 				var _this=this;
// 
// 					this.request({
// 					url: this.config.url+"getOpenid",
// 					data: {
// 						code: _this.code,
// 					},
// 					method: 'POST',
// 					success: (res) => {
// 					  console.log("getopenid",res)
// 				            if(res.data.code==2){
// 								_this.OpenId = res.data.data.openid;
// 							//	console.log("_this.Openid",_this.Openid);
// 								//_this.getinfo();
// 								_this.SessionKey = res.data.data.session_key;
// 							}
// 						
// 				
// 						uni.setStorage({
// 							key: "sessionkey",
// 							data: _this.SessionKey
// 						})
// 						//openId、或SessionKdy存储//隐藏loading
// 				
// 					}
// 				});
// 				
