			//	2.将用户登录code传递到后台置换用户SessionKey、OpenId等信息
				getOpenid(){
					
					this.request({
						url: this.config.url+"getOpenid",
						data: {
							code: this.code,
						},
						method: 'POST',
						success: (res) => {
							
							console.log(res)
							if(res.data.code==1){
								 this.OpenId=res.data.data.openid;
								 this.isCanUse=true;
							}else if(res.data.code==2){
							var userinfo=JSON.stringify(res.data.data)	
							uni.setStorage({
									key:"info",
									data:userinfo,
									success:()=>{
										
										uni.reLaunch({
											url:"/pages/tabBar/home"
										})
									}
								})
					    }
						}
					});
				},
			//向后台更新信息

	
			cancel() {
				this.writePhone = false;

			},
			//确认跳转home

			confirm(){
				    
                var _this=this;
				var result = this.phoneNumber.replace(/(^\s+)|(\s+$)/g,"");
				if(result.length<1){

					//console.log(2)
					uni.showToast({
						title: "请输入手机号",
						duration: 1000
					})
				} else if (!(/^1[3456789]\d{9}$/.test(this.phoneNumber))) {
					//console.log(3)
					uni.showToast({
						title: "请输入正确的手机号",
						duration: 1000
					})

				}else{

					this.request({
						url: _this.config.url + "reg",
						method: "POST",
						data: {
							openid: this.OpenId,
							headimg: this.avatarUrl,
							nickname: this.nickName,
							phone: this.phoneNumber
						},
						success: (res) => {
						//	console.log("reg", res)	;
							if (res.data.code == 1) {
								var userinfo = JSON.stringify(res.data.data)
								uni.setStorage({
									key: "info",
									data: userinfo,
									success: () => {
									
										uni.reLaunch({
											url: "/pages/tabBar/home"
										})
									}
								})
							}
						}
					})
				}
			}
		},
		onLoad() { //默认加载

			uni.getStorage({
				key: "info",
				success: (res) => {
					uni.reLaunch({
						url: "/pages/tabBar/home"
					})

				},
				fail: () => {

					this.login();
				}
			})

		}
